.super-img {
  width: 100%;
}

.un-photo {
  max-width: 50%;
  margin: auto;
  visibility: hidden;
  cursor: pointer;
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center center;
}
.un-photo > img {
  visibility: visible;
  display: block;
  -webkit-transition: opacity 0.3s linear, -webkit-transform 0.4s cubic-bezier(0.075, 0.82, 0.165, 1) !important;
  transition: opacity 0.3s linear, -webkit-transform 0.4s cubic-bezier(0.075, 0.82, 0.165, 1) !important;
  transition: opacity 0.3s linear, transform 0.4s cubic-bezier(0.075, 0.82, 0.165, 1) !important;
  transition: opacity 0.3s linear, transform 0.4s cubic-bezier(0.075, 0.82, 0.165, 1), -webkit-transform 0.4s cubic-bezier(0.075, 0.82, 0.165, 1) !important;
}
.un-photo:before {
  content: '';
  display: block;
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: #000;
  z-index: 2;
  visibility: visible;
  opacity: 0;
  -webkit-transition: opacity 0.4s linear;
  transition: opacity 0.4s linear;
  pointer-events: none;
}

.un-photo:hover {
  z-index: 999;
}
.un-photo:hover > img {
  opacity: 1;
  -webkit-transform: scale(1.2);
          transform: scale(1.2);
}

/* ---------------------------------- */
.un-photo__popup {
  z-index: 99;
  position: fixed;
  display: inline-block;
  width: 90%;
  max-width: 600px;
  max-height: 90vh;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  border: solid 1px red;
  pointer-events: none;
  background: inherit;
  visibility: visible;
  z-index: 999;
}
.un-photo__popup img {
  opacity: 1;
  width: auto;
  height: auto;
  height: 100%;
  width: 100%;
  outline: solid 1px blue;
}

@supports (--row-size: calc( 100% / var(--row-total) )) {
  .un-photo__popup {
    visibility: hidden;
    opacity: 0;
    -webkit-transition: opacity .4s linear;
    transition: opacity .4s linear;
  }

  .un-photo__row {
    --row-size: calc( 100% / var(--row-total) );
    position: absolute;
    width: 100%;
    left: 0%;
    top: calc( 0% + (var(--row-size) * var(--row-index)) );
    height: var(--row-size);
    background: inherit;
    overflow: hidden;
    -webkit-transform-origin: 0 0;
            transform-origin: 0 0;
    -webkit-transform-origin: calc( var(--random) * 50% ) 50%;
            transform-origin: calc( var(--random) * 50% ) 50%;
    -webkit-transform: scale(0.8) translate(0%, calc( var(--random) * 20% ));
            transform: scale(0.8) translate(0%, calc( var(--random) * 20% ));
    -webkit-transform: translate(0, 0) scale(1);
            transform: translate(0, 0) scale(1);
    -webkit-animation-name: photo-bounce;
            animation-name: photo-bounce;
    -webkit-animation-fill-mode: both;
            animation-fill-mode: both;
    -webkit-animation-direction: forwards;
            animation-direction: forwards;
    -webkit-animation-delay: 0.1s;
            animation-delay: 0.1s;
    -webkit-animation-duration: 0.4s;
            animation-duration: 0.4s;
    -webkit-animation-timing-function: steps(1);
            animation-timing-function: steps(1);
  }

  .un-photo__row-inner {
    opacity: 1;
    visibility: visible;
    position: absolute;
    background: inherit;
    width: 100%;
    height: calc(100% * var(--row-total));
    -webkit-transform: translateY(calc(-1 * ( 100% / var(--row-total) ) * var(--row-index)));
            transform: translateY(calc(-1 * ( 100% / var(--row-total) ) * var(--row-index)));
  }

  .un-photo:hover .un-photo__popup {
    opacity: 1;
  }
  .un-photo:hover:before {
    opacity: .4;
  }
  .un-photo:hover .un-photo__row {
    -webkit-animation-name: photo-bounce2;
            animation-name: photo-bounce2;
    -webkit-animation-duration: 0.3s;
            animation-duration: 0.3s;
    -webkit-animation-iteration-count: 1;
            animation-iteration-count: 1;
    animation-direction: reverse;
    -webkit-animation-timing-function: steps(1);
            animation-timing-function: steps(1);
  }
}
@-webkit-keyframes photo-bounce {
  25% {
    -webkit-transform: translate(-10%, 0%) scale(1);
            transform: translate(-10%, 0%) scale(1);
  }
  50% {
    -webkit-transform: translate(10%, -2%) scale(1.3);
            transform: translate(10%, -2%) scale(1.3);
  }
  75% {
    -webkit-transform: translate(-3%, 2%) scale(0.9);
            transform: translate(-3%, 2%) scale(0.9);
  }
}
@keyframes photo-bounce {
  25% {
    -webkit-transform: translate(-10%, 0%) scale(1);
            transform: translate(-10%, 0%) scale(1);
  }
  50% {
    -webkit-transform: translate(10%, -2%) scale(1.3);
            transform: translate(10%, -2%) scale(1.3);
  }
  75% {
    -webkit-transform: translate(-3%, 2%) scale(0.9);
            transform: translate(-3%, 2%) scale(0.9);
  }
}
@-webkit-keyframes photo-bounce2 {
  25% {
    -webkit-transform: translate(5%, 0%) scale(1);
            transform: translate(5%, 0%) scale(1);
  }
  50% {
    -webkit-transform: translate(-10%, -2%) scale(1.3);
            transform: translate(-10%, -2%) scale(1.3);
  }
  75% {
    -webkit-transform: translate(10%, 5%) scale(0.9);
            transform: translate(10%, 5%) scale(0.9);
  }
}
@keyframes photo-bounce2 {
  25% {
    -webkit-transform: translate(5%, 0%) scale(1);
            transform: translate(5%, 0%) scale(1);
  }
  50% {
    -webkit-transform: translate(-10%, -2%) scale(1.3);
            transform: translate(-10%, -2%) scale(1.3);
  }
  75% {
    -webkit-transform: translate(10%, 5%) scale(0.9);
            transform: translate(10%, 5%) scale(0.9);
  }
}
