.waves {
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  height: 100%;
  background-color: rgba(0,0,0,0);
  background-color: var(--color);
  background-image:url(/jack-design/static/media/wave.5fe38509.png);
  background-repeat:no-repeat;
  background-position:bottom center;
  background-size:100%;
  transition: 500ms;
}

.waves::before,
.waves::after {
  content: '';
  width: 100%;
  height: 100%;
}

.waves::before {
  border-radius: 44%;
  background: rgba(51, 51, 51, 1);
}

.waves::after {
  border-radius: 44%;
  background: rgba(51, 51, 51, 0.5);
}

